@import url('./variables.css');

.target_container {
  width: 300px;
  padding-top: 24px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin: 28px auto 0px auto;
  background-color: #FFFFFF;
  border-radius: 12px;
  box-shadow: 0 4px 6px -1px rgb(0 0 0 / 0.1), 0 2px 4px -2px rgb(0 0 0 / 0.1);
  
}

p {
  font-family: var(--Monserrat);
  text-align: center;
  margin: 0;
}

.level{
  color: var(--Grayish_Blue);
  font-size: 0.75em;
}

.text-base{
  color: var(--Grayish_Blue);
  padding: 10px 0;
  font-size: 0.75em;
  width: 80%;
  border-top: solid 2px var(--Light_Grayish_Blue);
}

.price {
  color: var(--Dark_Grayish_Blue);
  font-size: 2.5em;
  margin: 16px 0;
}

.target-button-container {
  padding: 22px 0;
  width: 80%;
  border-top: solid 2px var(--Light_Grayish_Blue);
}

.target-button {
  display: block;
  width: 100%;
  border: none;
  border-radius: 6px;
  font-family: var(--Monserrat);
  text-transform: uppercase;
  font-size: 0.75em;
  color: #FFFFFF;
  background: linear-gradient(45deg, hsl(236, 72%, 79%), hsl(237, 63%, 64%));
  padding: 12px 0;
  margin: 0 auto;
  cursor: pointer;
}

/* CASO ÃšNICO */

.profesional {
  background: linear-gradient(45deg, hsl(236, 72%, 79%), hsl(237, 63%, 64%));
}

.profesional-text {
  color: #FFFFFF;
  /* border-top: solid 2px #FFFFFF; */
}

.profesional-text-base {
  color: #FFFFFF;
  border-top: solid 2px #FFFFFF;
}

.profesional-button{
  background: #FFFFFF;
  color: hsl(237, 63%, 64%);
}

.profesional-container {
  border-top: solid 2px #FFFFFF;
}

/* M E D I A */

@media (min-width: 1440px) {
  .target_container {
    padding-top: 44px;
    margin: 0 4px;
  }

  .target-button-container {
    padding: 44px 0;
  }

  .level {
    font-size: 1em;
  }

  .price {
    font-size: 3em;
  }
  
  .text-base {
    border-top: solid 1px var(--Light_Grayish_Blue);
  }

  .profesional-text-base {
    border-top: solid 1px #FFFFFF;
  }

  .target-button-container {
    border-top: solid 1px var(--Light_Grayish_Blue);
  }
  .profesional-container {
    border-top: solid 1px #FFFFFF;
  }

  .profesional {
    padding-top: 48px;
    padding-bottom: 48px;
  }

  /* .target_container {

  } */
}